#contactsTitle {
  font-family: Playfair Display;
  font-size: 48px;
  line-height: 72px;
  color: #25282b;
  margin-top: 150px;
  margin-bottom: 38px;
}

#contactsPhone,
#contactsMail {
  font-family: Nunito;
  font-size: 24px;
  line-height: 150%;
  color: #25282b;
}

#divPhoneNumber,
#divMail {
  display: flex;
  align-items: center;
}

#phone,
#mail {
  width: 35px;
  padding-right: 15px;
}

#mail,
#contactsMail {
  margin-top: 40px;
}

.contact-form {
  margin: 3rem auto 0;
  max-width: 50%;
  max-width: 350px;
  font-family: Verdana, Geneva, Tahoma, sans-serif;
  border: 1px solid;
  background: #363434;
  padding: 0 30px 10px;
  box-shadow: 0px 4px 10px rgba(51, 51, 51, 0.637);
}

h2 {
  margin-top: 35px;
  text-align: center;
  color: whitesmoke;
}

.form-content {
  display: grid;
  grid-template-rows: repeat(3, 1fr) 10px;
}

input,
textarea {
  background: none;
  font-family: Verdana, Geneva, Tahoma, sans-serif;
  font-size: 1.1rem;
  border-bottom: 1px solid#cec950;
  color: whitesmoke;
}

input::placeholder,
textarea::placeholder {
  color: rgb(190, 188, 188);
}

input {
  padding: 24px 0 12px;
}

input,
.email-content,
textarea {
  width: 90%;
  margin: 0 auto;
}


.email-content {
  position: relative;
}

.email-content input {
  width: 100%;
}

#not-mail {
  display: none;
  position: absolute;
  top: 4px;
  color: #ff4d4d;
  right: 0;
  transform-origin: 50% 50%;
}

textarea {
  resize: none;
  padding: 24px 0;
}

.button {
  font-size: 1.3rem;
  margin-top: 15px;
  color: #cec950;
  border: none;
  cursor: pointer;
  transition: .2s;
  width: 100%;
}

.button:hover {
  letter-spacing: 2px;
}

.error::placeholder {
  color: #ff4d4d;
}

.form-message {
  margin-top: 10px;
  padding: 12px;
  opacity: 0;
  transition: 0.2s ease;
  color: black;
  border-radius: 4px;
  box-shadow: 0 0 2px rgba(51, 51, 51, 0.3);
}

input:focus, textarea:focus {
  outline: none;
}