@media only screen and (max-width: 510px) {

    #pdp {
        display: none;
    }

    #center {
        display: flex;
        justify-content: center;
        flex-direction: column;
        align-items: center;
    }

    #contactsTitle {
        font-family: Playfair Display;
        font-size: 48px;
        line-height: 72px;
        color: #25282b;
        margin-top: 100px;
        margin-bottom: 48px;
    }

    #contactsPhone, #phone {
        padding-bottom: 20px;
    }


    #allContacts {
        display: flex;
        justify-content: center;
        flex-direction: column;
        align-items: center;
    }

    #iconsContacts {
        position: relative;
        display: flex;
        justify-content: center;
        align-items: center;
        bottom: 0;
        padding: 50px;

    }

    #form {
        transform: scale(80%);
        width: 100%;
    }

    #aboutTitle {
        font-size: 34px;
    }

    #aboutDescription {
        font-size: 17px;
    }
}

