@media only screen and (max-width: 1306px) {

    #pdp {
        display: none;
    }

    


    #contactsTitle {
        font-family: Playfair Display;
        font-size: 48px;
        line-height: 72px;
        color: #25282b;
        margin-top: 100px;
        margin-bottom: 48px;
    }

    #contactsPhone, #phone {
        padding-bottom: 20px;
    }

    #center {
        display: flex;
        justify-content: center;
        flex-direction: column;
