}

@media only screen and (min-width:1306px) {
    #aboutDescription {
        width: 50%;

    }

    #iconsContacts {
        position: absolute;
        bottom: 0;
        right: 40%;
        left: 40%;
        display: flex;
        justify-content: center;
        align-items: center;
        bottom: 0;
        padding: 50px;

    }



    #form {
        position: absolute;
        width: 524px;
        height: 524px;
        right: 4%;
        top: 5%;
    }
}